import React from "react";
import WeatherItem from "../WeatherItem";
import "./WeekContainer.css";
import WeatherToday from "../WeatherToday";
// 

let a = {
  weather: 'ff'
}
//
// f9cf5753c97a8177043d72bc37c010fd
const weatherURL =
  "https://api.openweathermap.org/data/2.5/forecast?q=Simferopol&lang=ru&units=metric&APPID=f9cf5753c97a8177043d72bc37c010fd";

  const weatherURL2 = 
  "https://api.openweathermap.org/data/2.5/weather?q=Simferopol&lang=ru&units=metric&APPID=f9cf5753c97a8177043d72bc37c010fd";

  // api.openweathermap.org/data/2.5/weather?q=London&appid={API key}

class WeekContainer extends React.Component {
  state = {
    days: [],
    today: [],
  };

  componentDidMount = () => {
    fetch(weatherURL2)
    .then((res) => res.json())
    .then((data) => {
      console.log(data, 'data data');
      this.setState({ today: data });
    });


    fetch(weatherURL)
      .then((res) => res.json())
      .then((data) => {
        const dailyData = data.list.filter((reading) =>
          reading.dt_txt.includes("12:00:00")
        );
        this.setState({ days: dailyData });
      });
  };
      


  render() {
      return (
        <div className="wr">
          <div className="container">
            <div className="container-city">
              <p className="container-city__title">Simferopol</p>
            </div>
            <WeatherToday day={this.state.today} />
            {/* {this.state.days.length < 1 ? <p></p> :  <WeatherToday day={this.state.days[0]} />  */}
    {/* } */}
          {/* // <WeatherToday day={this.state.days[0]} : day={a}} />   */}
     

            <div className="cards">
              {this.state.days.map((day, index) => (
                <WeatherItem day={day} key={index} />
              ))}
              <div></div>
            </div>
          </div>
        </div>
      );
    // } else {
    //   return (
    //   <p>fff</p>
    //   )
    // }
  }
}

export default WeekContainer;
