import React from "react";
import "./WeatherItem.css";
import CloudsIcon from "../../img/icon/clouds.png";
import SunnyIcon from "../../img/icon/sunny.png";
import SnowIcon from "../../img/icon/snow.png";
import RainIcon from "../../img/icon/rain.png";
import RainfIcon from "../../img/icon/klipartz.com (1).png";
import RainIfdcon from "../../img/icon/klipartz.com (2).png";
import CloudSunny from "../../img/icon/klipartz.com (4).png";
import rrr from "../../img/icon/klipartz.com (4).png";
import CloudsIcon2 from "../../img/icon/klipartz.com (5).png";
import CloudsIcon3 from "../../img/icon/klipartz.com (6).png";

const WeatherItem = ({ day }) => {
  const ms = day.dt * 1000;
  const weekdayName = new Date(ms).toLocaleString("ru", { weekday: "long" });
  let imgSrc;

  const getSrcImg = () => {

    if (day.weather[0].description === "облачно с прояснениями") {
      imgSrc = CloudsIcon2;
    } else if (day.weather[0].description === "небольшой снег") {
      imgSrc = SnowIcon;
    } else if (day.weather[0].description === "пасмурно") {
      imgSrc = CloudsIcon;
    } else if (day.weather[0].description === "ясно") {
      imgSrc = SunnyIcon;
    } else if (day.weather[0].description === "небольшая облачность") {
      imgSrc = CloudsIcon3;
    } else if (day.weather[0].description === "переменная облачность") {
      imgSrc = CloudSunny;
    } else if (day.weather[0].description === "небольшой дождь") {
      imgSrc = RainIcon;
    } else {
      imgSrc = SnowIcon;
    }
  }

  getSrcImg();
  return (
    <div className="card-wp">
      <div className="card bg-light">
        <h3 className="card-title">{weekdayName}</h3>
        {/* <i className={imgURL}></i> */}
        <h2>{Math.round(day.main.temp)} °C</h2>
        <div className="desc">
          {/* {day.weather[0].description} */}
          <div className="desc__item">{day.weather[0].description}</div>
          <img src={imgSrc} alt="dfdf" />
        </div>
      </div>
    </div>
  );
};
// небольшая облачность
export default WeatherItem;
