// WeatherToday
import React from "react";
import "./WeatherToday.css";
import DownloadEdicator from '../DownloadEdicator';
import WeatherIcon from "../../img/icon/clouds.png";

const WeatherToday = ({ day }) => {

if (day.base) {
 
  // const ms = day.dt * 1000;
  // const weekdayName = new Date(ms).toLocaleString("ru", { weekday: "long" });
  // let data =  day.dt_txt.split("-");
  //  data = data[1] + data[2];
  // console.log(data, 'data');
 
  let d = new Date();
  let today = String(d).split(' ');

   return (
    <div className="container-day">
    <div className="container-weather">
      <img src={WeatherIcon} alt="icon" />
      <div className="temp">{Math.round(day.main.temp)}°C</div>
    </div>
    <div className="container-more">
      <div className="container-more__time">{today[0]} {today[2]} {today[1]}</div>

      <div className="container-temp">
      <span className="container-weather__info">Ощущается ~ {Math.round(day.main.feels_like)} °C </span>
       
        <div className="temp-max">{day.weather[0].description}</div>
      </div>
    </div>
  </div>
   );
}
else {
  return (
    <DownloadEdicator/>
   );
}

  

};

export default WeatherToday;